.s-usefull {
    padding: 46px 0;

    @media (max-width: 1199px) {
        padding: 37px 0;
    }

    @media (max-width: 991px) {
        padding: 28px 0;
    }

    @media (max-width: 600px) {
        padding: 20px 36px;
    }

    &__form {
        display: flex;
        align-items: center;

        @media (max-width: 1199px) {
            max-width: 826px;
            justify-content: space-between;
            margin: 0 auto;
        }
    }

    &__title {
        font-size: rem(40);
        line-height: 120%;
        color: #4E5056;
        margin-right: 70px;

        @media (max-width: 1199px) {
            font-size: 30px;
        }

        @media (max-width: 991px) {
            font-size: 20px;
            margin-right: 25px;
        }

        @media (max-width: 600px) {
            font-size: 15px;
        }
    }

    &__btns {
        display: flex;
        align-items: center;
    }

    &__btn {
        font-size: 12px;
        padding: 15px 22px;

        @media (max-width: 600px) {
            padding: 13px 14px;
        }
    }

    span {
        font-size: rem(40);
        color: #4E5056;
        margin: 0 17px;

        @media (max-width: 600px) {
            margin: 0 10px;
        }
    }
}
