.s-cases-cards {
    padding: 70px 0 150px;

    @media screen and (max-width: 1699px) {
        padding: 70px 0 0;
    }

    @media screen and (max-width: 1199px) {
        padding: 55px 0 0;
    }

    @media screen and (max-width: 991px) {
        padding: 40px 0 0;
    }

    @media screen and (max-width: 600px) {
        padding: 40px 0 32px;
    }

    &__wrapper {
        max-width: 1445px;
        margin: 0 auto;

        @media screen and (max-width: 1699px) {
            max-width: 1214px;
        }
    }

    &__title {
        font-size: 42px;
        line-height: 120%;
        font-weight: 300;
        letter-spacing: 0.77em;
        opacity: .4;
        color: #EBEBEF;
        margin-bottom: 30px;

        @media screen and (max-width: 1699px) {
            font-size: 37px;
        }

        @media screen and (max-width: 1199px) {
            font-size: 32px;
            margin-bottom: 25px;
        }

        @media screen and (max-width: 991px) {
            font-size: 28px;
        }

        @media screen and (max-width: 600px) {
            font-size: 17px;
            letter-spacing: 0.75em;
            margin-bottom: 20px;
        }
    }

    &__description {
        max-width: 268px;
        font-size: 14px;
        line-height: 120%;
        color: #C6C7D0;
        margin-bottom: 55px;

        @media screen and (max-width: 1699px) {
            max-width: 231px;
        }

        @media screen and (max-width: 1199px) {
            margin-bottom: rem(40);
        }

        @media screen and (max-width: 991px) {
            font-size: 13px;
            max-width: 317px;
            margin-bottom: rem(50);
        }

        @media screen and (max-width: 600px) {
            margin-bottom: 30px;
        }
    }

    &__tabs {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 66px;

        @media screen and (max-width: 1699px) {
            margin-bottom: 60px;
        }

        @media screen and (max-width: 1199px) {
            margin-bottom: 48px;
        }

        @media screen and (max-width: 991px) {
            margin-bottom: rem(40);
        }

        @media screen and (max-width: 600px) {
            margin-bottom: 9px;
        }
    }

    &__tab {
        position: relative;
        font-size: 17px;
        line-height: 120%;
        color: #656669;
        padding: 0 70px;

        @media screen and (max-width: 1699px) {
            padding: 0 54px;
        }

        @media screen and (max-width: 1199px) {
            font-size: 15px;
            padding: 0 34px;
        }

        @media screen and (max-width: 991px) {
            font-size: 12px;
            padding: 0 27px;
        }

        @media screen and (max-width: 600px) {
            font-size: 10px;
            padding: 0;
            margin-right: 18px;
            margin-bottom: 11px;
        }

        &:first-child {
            padding-left: 0;
        }

        &:last-child {
            padding-right: 0;

            @media screen and (max-width: 600px) {
                margin-right: 0;
            }

            &:after {
                display: none;
            }
        }

        &:after {
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
            content: '';
            width: 1px;
            height: 15px;
            display: block;
            background: #656669;

            @media screen and (max-width: 600px) {
                right: -9px;
            }
        }

        &.active, &:hover {
            color: #CECED0;
        }
    }

    &__items {
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        margin: 0 -75px;

        @media screen and (max-width: 1699px) {
            margin: 0 -108px;
        }

        @media screen and (max-width: 1199px) {
            margin: 0 -37px;
        }

        @media screen and (max-width: 991px) {
            margin: 0 -15px;
        }

        @media screen and (max-width: 600px) {
            margin: 0;
        }
    }

    .s-cases-card, .s-cases-cards__items-more {
        width: calc(50% - 150px);
        margin: 0 75px 210px;

        @media screen and (max-width: 1699px) {
            width: calc(50% - 216px);
            margin: 0 108px 240px;
        }

        @media screen and (max-width: 1199px) {
            width: calc(50% - 74px);
            margin: 0 37px 140px;
        }

        @media screen and (max-width: 991px) {
            width: calc(50% - 30px);
            margin: 0 15px 140px;
        }

        @media screen and (max-width: 600px) {
            width: 100%;
            margin: 0 0 60px;
        }

        &:nth-child(2n) {
            transform: translateY(326px);

            @media screen and (max-width: 1199px) {
                transform: translateY(169px);
            }

            @media screen and (max-width: 600px) {
                transform: translateY(0);
            }
        }

        &:last-child {
            @media screen and (max-width: 600px) {
                margin: 0 !important;
                margin-bottom: 0;
            }
        }
    }

    .s-cases-cards__items-more {
        margin-top: 95px;
        margin-bottom: 0;

        @media screen and (max-width: 1699px) {
            margin-top: 68px;
        }

        @media screen and (max-width: 1199px) {
            margin-top: 12px;
        }

        @media screen and (max-width: 600px) {
            display: none;
        }
    }

    &__btn {
        position: relative;
        display: none;

        @media screen and (max-width: 600px) {
            display: block;
            padding-top: 101px;
            padding-bottom: 148px;

            &-bordered {
                padding: 13px 30px;
                margin: 0 auto;
            }

            &-text {
                position: absolute;
                z-index: -1;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                font-size: 250px;
                line-height: 1;
                letter-spacing: -0.05em;
                opacity: .02;
                color: #CECED0;
            }
        }
    }
}
