@import "helpers/index";
@import "varibles/index";
// @import "../blocks/modules/services/s_services_cards/s_services_cards";
@import "../blocks/modules/s_cases_cards/s_cases_cards";
@import "../blocks/modules/s_usefull/s_usefull";

.s-banner {
    overflow: hidden;

    &__title {
        max-width: rem(685);
        margin-bottom: rem(15);
        color: #9a9ea9;
        -webkit-text-fill-color: #9a9ea9;

        @media (min-width: 768px) {
            margin-bottom: rem(29);
        }

        @media (min-width: 1024px) {
            margin-bottom: rem(40);
        }

        @media (min-width: 1600px) {
            margin-bottom: rem(70);
        }
    }
}

.main {
    position: relative;
    // background-color: #000203; // оставляем для дальнейших фонов, что бы не было претензий я придумал как

    &:after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background-image: url(".././img/about/bg-page.png");
        background-size: contain;
        background-repeat: no-repeat;

        @media screen and (max-width: 767px) {
            height: 10%;
            background-size: cover;
        }
    }
}
